var NEM_PORT = ":7891";

var NEM_UNIT = 1000000;

var dispTimeStamp = function dispTimeStamp(timeStamp) {
    var NEM_EPOCH = Date.UTC(2015, 2, 29, 0, 6, 25, 0);
    var d = new Date(NEM_EPOCH + timeStamp * 1000);
    return d.toLocaleString();
};

var getTargetNode = function getTargetNode() {
    return NODES[Math.floor(Math.random() * NODES.length)] + NEM_PORT;
};

function getAccountTransfersURL(address) {
    var targetNode = getTargetNode();
    var ret = targetNode + "/account/transfers/all?address=" + address;
    return ret;
}

function getUnconfirmedTransactionURL(address) {
    var targetNode = getTargetNode();
    var ret = targetNode + "/account/unconfirmedTransactions?address=" + address;
    return ret;
}

function setDashbordList(dashbordList, tranJsonData, address, unconfirmedFlag) {
    var arrLen = tranJsonData.data.data.length;
    for (var i = 0; i < arrLen; i++) {
        var tran = tranJsonData.data.data[i].transaction;
        var meta = tranJsonData.data.data[i].meta;
        var ts = dispTimeStamp(tran.timeStamp);
        var tp = '';
        var am = 0;
        var ul = '';
        var cl = '';
        var tran_amount = 0;
        var bgc = '';
        if (tran.type == 4100) {
            tran = tran.otherTrans;
        }
        if (tran.type == 257 || tran.type == 8193) {
            var has_mosaic = false;
            //モザイクが存在した場合
            if (tran.mosaics) {
                tran.mosaics.forEach(function (key) {
                    has_mosaic = true;
                    var mosaic = key;
                    if (mosaic.mosaicId.name == "xem" && mosaic.mosaicId.namespaceId == "nem") {
                        tran_amount = mosaic.quantity;
                    }
                });
            }
            //通常送金
            if (!has_mosaic) {
                if (tran.type == 8193) {
                    tran_amount = tran.rentalFee;
                } else {
                    tran_amount = tran.amount;
                }
            }
            if (address != tran.recipient) {
                tp = '出金';
                am = '- ' + ((tran_amount + tran.fee) / NEM_UNIT).toFixed(6);
                cl = 'text-danger';
            } else {
                tp = '入金';
                am = '+ ' + (tran_amount / NEM_UNIT).toFixed(6);
                cl = 'text-success';
            }
            if (unconfirmedFlag) {
                bgc = 'table-warning';
            } else {
                ul = 'http://explorer.nemchina.com/#/s_tx?hash=' + meta.hash.data;
            }
            dashbordList.push({
                timeStamp: ts,
                type: tp,
                amount: am,
                url: ul,
                color: cl,
                unconfirmed: unconfirmedFlag,
                bgcolor: bgc
            });
        }
    }
    return dashbordList;
}

